.placeholder, .placeholder-over, .placeholder-active, .placeholder-disabled {
  display: block;
  width: 100%;
  padding: 0 0.5em;
  border: 1px solid rgba(77, 77, 77, 0.1);
  background-color: white;
  line-height: 20px;
  border-radius: 3px;
  text-overflow: ellipsis;
  font-weight: normal;
  color: #ccc; }

.placeholder-over, .placeholder-active {
  border: 1px dashed #4CAF50;
  background-color: #d0efb3;
  color: #212121; }

.placeholder-active {
  background-color: #C8E6C9; }

.placeholder-disabled {
  background-color: #e0e0e0;
  color: grey; }

.filter-shelf {
  background-color: white;
  border-radius: 3px;
  border: 1px solid #ddd;
  margin-bottom: 4px;
  max-height: 150px;
  overflow: scroll; }

.filter-pane {
  max-height: calc(40vh);
  overflow-y: auto;
  overflow-x: hidden; }
